.splide__arrow {
  position: absolute;
  z-index: 1;
  top: 50%;
  transform: translateY(-50%);
  width: 20px;
  height: 20px;
  border-radius: 50%;
  display: flex;
  align-items: center;
  justify-content: center;
  border: none;
  padding: 0;
  opacity: 0.5;
  background: transparent; }

.splide--result .splide__arrow {
  top: 50%; }

.splide__arrow svg {
  width: 20px;
  height: 20px;
  fill: var(--text-basic); }

.splide__arrow:hover {
  cursor: pointer; }

.splide__arrow:focus {
  outline: none; }

.splide__arrow:focus-visible {
  outline: auto; }

.splide__arrow:active svg {
  fill: var(--accent); }

.splide__arrow--prev {
  left: -20px; }

.splide__arrow--prev svg {
  transform: scaleX(-1); }

.splide__arrow--next {
  right: -20px; }

.splide__pagination {
  position: absolute;
  z-index: 1;
  bottom: 0;
  left: 50%;
  transform: translateX(-50%);
  padding: 0; }

.splide__pagination__page {
  display: inline-block;
  width: 10px;
  height: 10px;
  background: #cccccc;
  border-radius: 50%;
  margin: 0 10px;
  padding: 0;
